@import "../base/_variable.less";
main{
    margin-top: 70px;
    background: rgb(195,254,255);
    background: linear-gradient(126deg, rgb(203, 246, 247) 0%, rgba(218,204,252,1) 34%, rgb(225, 160, 235) 64%, rgb(228, 192, 115) 100%);
    .about-me{
        margin-top: 5rem;
        &__container{
            display: flex;
            justify-content: space-between;
            &__info{
                width: 45%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }
            &__image{
                width: 35%;
                border-radius: 10px;
                overflow: hidden;
                img{
                    display: block;
                    margin-right: 0;
                    height: 120% !important;
                    background-color: #fff;
                }
            }
        }
    }
}