*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
}
body{
    font-size: 16px;
    font-family: sans-serif;
}
ul, li{
    list-style-type: none;
}
a{
    text-decoration: none;
}
img, video{
    width: 100% !important;
    height: auto;
}
button{
    cursor: pointer;
}